<template>
  <div class="chat-recurso mx-5">
    <v-progress-circular v-if="cargando" color="primary" class="ml-2 mr-2 mb-5" indeterminate></v-progress-circular>
    <div v-else class="chat-creacion">
      <v-layout
        class="contenedor-conocimiento elevation-3 pt-1 pb-2 px-4"
        v-for="(conocimiento, c_idx) in conocimientos"
        :key="c_idx"
      >
        <v-flex>
          <div v-for="(pregunta, p_idx) in conocimiento.preguntas" :key="p_idx">
            <v-textarea
              v-if="p_idx==0 || conocimiento._mostrarDetalle"
              v-model="conocimiento.preguntas[p_idx]"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Pregunta"
              style="margin-bottom: 4px"
              @change="mostrarEntidades($event, c_idx, p_idx, -1)"
              @keyup="mostrarEntidades($event, c_idx, p_idx, -1)"
              @click="mostrarEntidades($event, c_idx, p_idx, -1)"
            >
              <template v-slot:append-outer>
                <v-btn
                  v-if="p_idx==0"
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="green"
                  @click="agregarPregunta(c_idx); conocimiento._mostrarDetalle = true"
                >
                  <v-icon dark>mdi-plus</v-icon>
                </v-btn>
                <v-btn
                  v-else
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="red"
                  @click="eliminarPregunta(c_idx, p_idx)"
                >
                  <v-icon dark>mdi-minus</v-icon>
                </v-btn>
              </template>
            </v-textarea>
            <v-card
              v-if="mostrar_entidades && c_idx_sel == c_idx && p_idx_sel == p_idx"
              class="menu-entidades mb-5"
            >
              <div class="d-flex justify-space-between align-center">
                <span class="px-3" style="color: #888">Seleccionar Entidad</span>
                <v-btn text icon @click="mostrar_entidades = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </div>
              <v-divider></v-divider>

              <div class="ma-2">
                <div v-for="(entidad, e_idx) in entidades" :key="e_idx">
                  <div v-if="entidad.nombre != 'perfil'">
                    <div
                      class="px-2 py-1 menu-entidades-nombre d-flex justify-space-between align-center"
                      @click="entidad.mostrar = !entidad.mostrar"
                    >
                      <span>{{entidad.nombre}}</span>
                      <v-btn v-if="!entidad.mostrar" text icon small color="success">
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                      <v-btn v-else text icon small color="error">
                        <v-icon>mdi-minus</v-icon>
                      </v-btn>
                    </div>
                    <div class="pl-5" v-if="entidad.mostrar">
                      <div
                        class="px-2 py-1 menu-entidades-columna"
                        v-for="(columna, c_idx) in entidad.columnas"
                        @click="seleccionarEntidad(entidad, columna, 'P')"
                        :key="c_idx"
                      >{{columna}}</div>
                    </div>
                  </div>
                </div>
              </div>
            </v-card>
          </div>
        </v-flex>
        <v-flex>
          <div v-for="(respuesta, r_idx) in conocimiento.respuestas" :key="r_idx">
            <v-textarea
              v-if="r_idx==0 || conocimiento._mostrarDetalle"
              v-model="conocimiento.respuestas[r_idx]"
              auto-grow
              hide-details
              rows="1"
              placeholder="Respuesta"
              style="margin-bottom: 4px"
              @change="mostrarEntidades($event, c_idx, -1, r_idx)"
              @keyup="mostrarEntidades($event, c_idx, -1, r_idx)"
              @click="mostrarEntidades($event, c_idx, -1, r_idx)"
            >
              <template v-slot:append-outer>
                <v-btn
                  v-if="r_idx==0"
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="green"
                  @click="agregarRespuesta(c_idx); conocimiento._mostrarDetalle = true"
                >
                  <v-icon dark>mdi-plus</v-icon>
                </v-btn>
                <v-btn
                  v-else
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="red"
                  @click="eliminarRespuesta(c_idx, r_idx)"
                >
                  <v-icon dark>mdi-minus</v-icon>
                </v-btn>
              </template>
            </v-textarea>
            <v-card
              v-if="mostrar_entidades && c_idx_sel == c_idx && r_idx_sel == r_idx"
              class="menu-entidades mb-5"
            >
              <div class="d-flex justify-space-between align-center">
                <span class="px-3" style="color: #888">Seleccionar Entidad</span>
                <v-btn text icon @click="mostrar_entidades = false">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </div>
              <v-divider></v-divider>

              <div class="ma-2">
                <div v-for="(entidad, e_idx) in entidades" :key="e_idx">
                  <div
                    class="px-2 py-1 menu-entidades-nombre d-flex justify-space-between align-center"
                    @click="entidad.mostrar = !entidad.mostrar"
                  >
                    <span>{{entidad.nombre}}</span>
                    <v-btn v-if="!entidad.mostrar" text icon small color="success">
                      <v-icon>mdi-plus</v-icon>
                    </v-btn>
                    <v-btn v-else text icon small color="error">
                      <v-icon>mdi-minus</v-icon>
                    </v-btn>
                  </div>
                  <div class="pl-5" v-if="entidad.mostrar">
                    <div
                      class="px-2 py-1 menu-entidades-columna"
                      v-for="(columna, c_idx) in entidad.columnas"
                      @click="seleccionarEntidad(entidad, columna, 'R')"
                      :key="c_idx"
                    >{{columna}}</div>
                  </div>
                </div>
              </div>
            </v-card>
          </div>
          <div v-if="conocimiento._mostrarDetalle || conocimiento.material_nombre">
            <div class="d-flex mt-4">
              <v-text-field
                v-model="conocimiento.material_nombre"
                auto-grow
                hide-details
                rows="1"
                placeholder="ID Material Asociado"
                solo
                readonly
                append-icon="mdi-close"
                @click:append="
                conocimiento.material_id=''
                conocimiento.material_nombre=''"
              ></v-text-field>
              <v-menu offset-y>
                <template v-slot:activator="{ on }">
                  <v-btn class="mt-2 ml-2" icon color="primary" dark v-on="on">
                    <v-icon>mdi-file-document</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    v-for="(material, m_idx) in materiales"
                    :key="m_idx"
                    @click="asignarMaterial(conocimiento, material)"
                  >
                    <v-list-item-title>{{ material.nombre }}</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </div>
          </div>
        </v-flex>
        <div class="pt-2">
          <v-btn
            text
            icon
            small
            color="primary"
            @click="conocimiento._mostrarDetalle = !conocimiento._mostrarDetalle"
          >
            <v-icon v-if="conocimiento._mostrarDetalle" dark>mdi-chevron-up</v-icon>
            <v-icon v-else dark>mdi-chevron-down</v-icon>
          </v-btn>
          <v-btn
            v-if="conocimientos.length > 2"
            text
            icon
            small
            color="error"
            @click="dialog_eliminar_conocimiento = true; 
            idx_eliminar = c_idx; 
            conocimiento_id_eliminar = conocimiento.conocimiento_id"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
      </v-layout>
    </div>
    <div class="mb-3">
      <v-btn color="success" class="ma-2 white--text" @click="agregarConocimiento">
        Agregar
        <v-icon dark>mdi-plus</v-icon>
      </v-btn>

      <v-btn
        :loading="loading_entrenar"
        color="blue"
        class="ma-2 white--text"
        @click="guardarCambios"
      >
        Guardar y Entrenar
        <v-icon right dark>mdi-content-save</v-icon>
      </v-btn>
    </div>

    <v-dialog v-model="dialog_eliminar_conocimiento" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">Desea eliminar el conocimiento seleccionado?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_eliminar_conocimiento = false; eliminarConocimiento()"
              color="red"
            >Eliminar</v-btn>
            <v-btn text @click="dialog_eliminar_conocimiento = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Conocimiento from "../../models/Conocimiento.js";
import Entidad from "../../models/Entidad.js";
import Material from "../../models/Material.js";

export default {
  name: "PreguntasRespuestas",
  data() {
    return {
      tema_id: "",
      cargando: true,
      loading_entrenar: false,
      entidades: [],
      conocimientos: [],
      conocimientos_ids_eliminar: [],
      /* Eliminar Conocimiento */
      dialog_eliminar_conocimiento: false,
      idx_eliminar: "",
      conocimiento_id_eliminar: "",
      materiales: [],
      /* Mostrar Entidades */
      mostrar_entidades: false,
      c_idx_sel: null,
      p_idx_sel: null,
      r_idx_sel: null,
      input_selected: null,
      selected_word: ""
    };
  },
  mounted() {
    this.tema_id = this.$route.params["tema_id"];
    this.obtenerConocimientos();
    this.obtenerEntidades();
  },
  methods: {
    mostrarEntidades(e, c_idx, p_idx, r_idx) {
      this.c_idx_sel = c_idx;
      this.p_idx_sel = p_idx;
      this.r_idx_sel = r_idx;

      if (e.target) this.input_selected = e.target;

      let selected_word = this.selectedWordByCursor(
        this.input_selected.value,
        this.input_selected.selectionStart
      )[2];

      if (selected_word[0] == "@") {
        this.mostrar_entidades = true;
      } else {
        this.mostrar_entidades = false;
      }
    },
    seleccionarEntidad(entidad, columna, tipo) {
      let value = this.input_selected.value;
      let aux = this.selectedWordByCursor(
        value,
        this.input_selected.selectionStart
      );
      let start = aux[0];
      let end = aux[1];
      value =
        value.substring(0, start) +
        " @" +
        entidad.nombre +
        "@" +
        columna +
        value.substring(end);

      if (tipo == "P") {
        let preguntas_clone = JSON.parse(
          JSON.stringify(this.conocimientos[this.c_idx_sel].preguntas)
        ); //Necesario para reactividad
        preguntas_clone[this.p_idx_sel] = value;
        this.conocimientos[this.c_idx_sel].preguntas = preguntas_clone;

        this.mostrar_entidades = false;
      } else if (tipo == "R") {
        let respuestas_clone = JSON.parse(
          JSON.stringify(this.conocimientos[this.c_idx_sel].respuestas)
        ); //Necesario para reactividad
        respuestas_clone[this.r_idx_sel] = value;
        this.conocimientos[this.c_idx_sel].respuestas = respuestas_clone;

        this.mostrar_entidades = false;
      }
    },
    selectedWordByCursor(aString, aPos) {
      let Nstr = aString.length;
      if (aString[aPos] == " ") aPos--;
      var start = aPos;
      var end = aPos;

      while (start > 0 && aString[start] != " ") start--;
      while (end < Nstr && aString[end] != " ") end++;
      return [start, end, aString.substring(start, end).trim()];
    },
    obtenerConocimientos() {
      this.$store.state.servicio.conocimiento.obtenerConocimientoPorTema(
        this.tema_id,
        response => {
          this.conocimientos = [];
          this.cargando = false;
          JSON.parse(response).forEach(c => {
            let conocimiento = new Conocimiento(
              c._id.$oid,
              c.material_id.$oid || "",
              c.preguntas,
              c.respuestas
            );
            this.conocimientos.push(conocimiento);
          });
          this.obtenerMateriales();
        },
        error => {
          console.log(error);
        }
      );
    },
    obtenerEntidades() {
      let profesor_id = this.$store.state.servicio.sesion.obtenerSesion()["id"];
      /* Perfil */
      this.$store.state.servicio.profesor.obtenerProfesorPorId(
        profesor_id,
        response => {
          this.entidades = [];
          this.entidades.push(
            new Entidad("", "perfil", Object.keys(response), "")
          );
          /* Entidades */
          this.$store.state.servicio.entidad.obtenerEntidadPorTema(
            this.tema_id,
            response => {
              this.entidades_loading = false;
              JSON.parse(response).forEach(e => {
                this.entidades.push(
                  new Entidad(e._id.$oid, e.nombre, e.columnas, e.datos)
                );
              });
            },
            error => {
              this.materiales_loading = false;
              console.log(error);
            }
          );
        },
        error => {
          console.log(error);
        }
      );
    },
    obtenerMateriales() {
      this.$store.state.servicio.material.obtenerMaterialPorTema(
        this.tema_id,
        response => {
          this.materiales = JSON.parse(response).map(m => {
            return new Material(
              m._id.$oid,
              m.nombre,
              m.texto,
              m.ejemplo,
              m.documento,
              m.video,
              m.imagen
            );
          });

          this.conocimientos.forEach(c => {
            let c_id = c.material_id;
            c.material_id = ""; // Vaco por defecto hasta que encuentre si existe el material
            this.materiales.forEach(m => {
              if (m.material_id == c_id) {
                // Si no se encuentra el material es que no existe y queda vaco
                c.material_id = c_id;
                c.material_nombre = m.nombre;
              }
            });
          });
        },
        error => {
          console.log(error);
        }
      );
    },
    asignarMaterial(conocimiento, material) {
      conocimiento.material_id = material.material_id;
      conocimiento.material_nombre = material.nombre;
    },
    entrenarChatbot() {
      this.entrenar = false;
      this.$store.state.servicio.entrenar(
        this.tema_id,
        response => {
          // this.$emit("showDialog", `Entrenado en un ${Math.floor(response * 100)}%`);
          this.loading_entrenar = false;
        },
        error => {
          console.log(error);
          this.loading_entrenar = false;
        }
      );
    },
    guardarCambios() {
      /* Corregir this.guardar */
      this.loading_entrenar = true;
      let promises = [];

      /* ELIMINAR CONOCIMIENTO */
      this.conocimientos_ids_eliminar.forEach(conocimiento_id => {
        promises.push(
          new Promise((res, rej) => {
            this.$store.state.servicio.conocimiento.eliminarConocimiento(
              conocimiento_id,
              response => {
                res();
              },
              error => {
                console.log(error);
                rej();
              }
            );
          })
        );
      });
      this.conocimientos_ids_eliminar = [];
      /************************************************************/
      this.conocimientos.forEach(c => {
        promises.push(
          new Promise((res, rej) => {
            if (c.conocimiento_id !== "") {
              /* ACTUALIZAR CONOCIMIENTO */
              this.$store.state.servicio.conocimiento.actualizarConocimiento(
                c.conocimiento_id,
                this.tema_id,
                c.material_id,
                c.preguntas,
                c.respuestas,
                response => {
                  res();
                },
                error => {
                  console.log(error);
                  rej();
                }
              );
            } else {
              c.profesor_id = this.$store.state.profesor_id;
              c.curso_id = this.$store.state.curso_id;
              /* INGRESAR CONOCIMIENTO */
              this.$store.state.servicio.conocimiento.ingresarConocimiento(
                this.tema_id,
                c.material_id,
                c.preguntas,
                c.respuestas,
                response => {
                  c.conocimiento_id = response.conocimiento_id;
                  res();
                },
                error => {
                  console.log(error);
                  rej();
                }
              );
            }
          })
        );
      });

      Promise.all(promises).then(res => {
        this.entrenarChatbot();
      });
    },
    agregarConocimiento() {
      this.conocimientos.push(new Conocimiento("", "", [""], [""]));
    },
    eliminarConocimiento() {
      this.conocimientos.splice(this.idx_eliminar, 1);
      if (this.conocimiento_id_eliminar !== "")
        this.conocimientos_ids_eliminar.push(this.conocimiento_id_eliminar);
    },

    agregarPregunta(c_idx) {
      this.conocimientos[c_idx].preguntas.push("");
    },
    eliminarPregunta(c_idx, p_idx) {
      this.conocimientos[c_idx].preguntas.splice(p_idx, 1);
    },

    agregarRespuesta(r_idx) {
      this.conocimientos[r_idx].respuestas.push("");
    },
    eliminarRespuesta(c_idx, r_idx) {
      this.conocimientos[c_idx].respuestas.splice(r_idx, 1);
    }
  }
};
</script>

<style scoped>
.chat-recurso {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.chat-creacion {
  width: 100%;
  height: 100%;
  padding: 32px 16px 0 16px;
  overflow-y: auto;
}
.contenedor-conocimiento {
  margin-bottom: 10px !important;
  border-radius: 4px;
}
/* Menu entidades */
.menu-entidades {
  position: absolute;
  z-index: 1;
}
.menu-entidades-nombre:hover {
  background: #e9e9e9;
  cursor: pointer;
}
.menu-entidades-columna:hover {
  background: #e9e9e9;
  cursor: pointer;
}
</style>