<template>
  <v-content class="chat-vista">
    <Cabecera />
    <v-container fluid grid-list-xl>
      <v-card>
        <v-tabs class="contenedor-creacion" background-color="#fff">
          <v-tab>
            <v-icon left>mdi-shape-plus</v-icon>Recursos
          </v-tab>
          <v-tab>
            <v-icon left>mdi-format-list-checks</v-icon>Cuestionarios
          </v-tab>
          <v-tab @click="iniciarPreguntas()">
            <v-icon left>mdi-forum</v-icon>Preguntas
          </v-tab>
          <v-tab>
            <v-icon left>mdi-table-large</v-icon>Entidades
          </v-tab>
          <v-tab @click="iniciarChatbot()">
            <v-icon left>mdi-robot</v-icon>Chatbot
          </v-tab>

          <v-tab-item>
            <CreacionRecursos />
          </v-tab-item>
          <v-tab-item>
            <CreacionCuestionario />
          </v-tab-item>
          <v-tab-item>
            <CreacionQA @showDialog="showDialog" ref="qa" />
          </v-tab-item>
          <v-tab-item>
            <CreacionEntidades />
          </v-tab-item>
          <v-tab-item>
            <Chatbot ref="chatbot" style="height: calc(100vh - 150px)" />
          </v-tab-item>
        </v-tabs>
      </v-card>
    </v-container>
  </v-content>
</template>

<script>
import Cabecera from "../components/Cabecera.vue";
import CreacionQA from "../components/Creacion/CreacionQA.vue";
import CreacionEntidades from "../components/Creacion/CreacionEntidades.vue";
import CreacionRecursos from "../components/Creacion/CreacionRecursos.vue";
import CreacionCuestionario from "../components/Creacion/CreacionCuestionario.vue";
import Chatbot from "../components/Creacion/Chatbot.vue";

export default {
  components: {
    Cabecera,
    CreacionQA,
    CreacionEntidades,
    CreacionRecursos,
    CreacionCuestionario,
    Chatbot
  },
  methods: {
    showDialog(text) {
      this.$emit("showDialog", text);
    },
    iniciarPreguntas() {
      // Actualizamos la lista de materiales al entrar al componente
      let qa_component = this.$refs["qa"];
      if (qa_component) {
        qa_component.obtenerConocimientos();
        qa_component.obtenerEntidades();
        qa_component.obtenerMateriales();
      }
    },
    iniciarChatbot() {
      // Actualizamos los materiales y cuestionarios al entrar al componente
      let chatbot_component = this.$refs["chatbot"];
      if (chatbot_component) {
        chatbot_component.obtenerMateriales();
        chatbot_component.obtenerCuestionarioPorTema();
      }
    }
  }
};
</script>

<style lang="scss">
.chat-vista {
  height: 100vh;
}
.contenedor-creacion {
  min-height: calc(100vh - 90px);
}
</style>
