<template>
  <div v-if="entidad_borrador" class="pb-1">
    <v-toolbar dense flat>
      <v-btn icon class="hidden-xs-only" @click="entidad_borrador = null">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-text-field hide-details v-model="entidad_borrador.nombre"></v-text-field>
      <div class="flex-grow-1"></div>

      <v-btn
        :loading="eliminar_loading"
        v-if="entidad_borrador.entidad_id"
        icon
        @click="dialog_eliminar_entidad = true"
      >
        <v-icon>mdi-delete</v-icon>
      </v-btn>
      <v-btn :loading="guardar_loading" icon @click="actualizarEntidad()">
        <v-icon>mdi-content-save</v-icon>
      </v-btn>
    </v-toolbar>
    <div class="mx-5 my-3">
      <div class="contenedor-entidades">
        <div class="contenedor-entidad elevation-3">
          <v-simple-table>
            <thead>
              <tr>
                <th></th>
                <th v-for="(columna, c_idx) in entidad_borrador.columnas" :key="c_idx">
                  <v-text-field hide-details v-model="entidad_borrador.columnas[c_idx]">
                    <v-btn
                      v-if="entidad_borrador.columnas.length > 1"
                      slot="append"
                      class="mx-2"
                      text
                      icon
                      color="red"
                      @click="eliminarColumna(c_idx)"
                    >
                      <v-icon dark>mdi-minus</v-icon>
                    </v-btn>
                  </v-text-field>
                </th>
                <th>
                  <v-btn class="mx-2" text icon color="green" @click="aumentarColumna()">
                    <v-icon dark>mdi-plus</v-icon>
                  </v-btn>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(fila, f_idx) in entidad_borrador.datos" :key="f_idx">
                <td>
                  <v-btn
                    v-if="entidad_borrador.datos.length > 1"
                    class="mx-2"
                    text
                    icon
                    color="red"
                    @click="eliminarFila(f_idx)"
                  >
                    <v-icon dark>mdi-minus</v-icon>
                  </v-btn>
                </td>
                <td v-for="(dato, d_idx) in fila" :key="d_idx">
                  <v-text-field hide-details v-model="fila[d_idx]" />
                </td>
              </tr>
              <tr>
                <td>
                  <v-btn class="mx-2" text icon color="green" @click="aumentarFila()">
                    <v-icon dark>mdi-plus</v-icon>
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </div>
      </div>
    </div>
    <v-dialog v-model="dialog_eliminar_entidad" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">Desea eliminar la entidad seleccionada?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_eliminar_entidad = false; eliminarEntidad()"
              color="red"
            >Eliminar</v-btn>
            <v-btn text @click="dialog_eliminar_entidad = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
  </div>
  <div v-else class="mx-3">
    <v-container fluid>
      <v-row>
        <v-col cols="12" sm="6" md="3" v-for="(entidad, e_idx) in entidades" :key="e_idx">
          <v-card class="cartel cartel-green" v-ripple dark flat @click="seleccionarEntidad(e_idx)">
            <span>{{entidad.nombre}}</span>
          </v-card>
        </v-col>
        <v-col v-if="entidades_loading" cols="12" sm="6" md="3">
          <div class="cartel-loading text-center">
            <v-progress-circular :size="30" color="#b3a238" indeterminate></v-progress-circular>
          </div>
        </v-col>
        <v-col cols="12" sm="6" md="3">
          <div class="cartel-agregar text-center" @click="agregarEntidad()">
            <span>+</span>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      entidad_borrador: null,
      entidades: [],
      defaultValue: "",
      //Loadings
      eliminar_loading: false,
      entidades_loading: true,
      guardar_loading: false,
      //Dialog
      dialog_eliminar_entidad: false
    };
  },
  mounted() {
    this.tema_id = this.$route.params["tema_id"];
    this.obtenerEntidades();
  },
  methods: {
    obtenerEntidades() {
      this.$store.state.servicio.entidad.obtenerEntidadPorTema(
        this.tema_id,
        response => {
          this.entidades_loading = false;
          JSON.parse(response).forEach(e => {
            this.entidades.push(
              new Entidad(e._id.$oid, e.nombre, e.columnas, e.datos)
            );
          });
        },
        error => {
          this.materiales_loading = false;
          console.log(error);
        }
      );
    },
    seleccionarEntidad(e_idx) {
      this.entidad_borrador = JSON.parse(JSON.stringify(this.entidades[e_idx]));
    },
    aumentarColumna() {
      this.entidad_borrador.columnas.push(this.defaultValue);
      this.entidad_borrador.datos.forEach(fila => {
        fila.push(this.defaultValue);
      });
    },
    eliminarColumna(c_idx) {
      this.entidad_borrador.columnas.splice(c_idx, 1);
      this.entidad_borrador.datos.forEach(fila => {
        fila.splice(c_idx, 1);
      });
    },
    aumentarFila() {
      this.entidad_borrador.datos.push(
        new Array(this.entidad_borrador.columnas.length).fill(this.defaultValue)
      );
    },
    eliminarFila(f_idx) {
      this.entidad_borrador.datos.splice(f_idx, 1);
    },
    agregarEntidad() {
      let entidad = new Entidad(
        "",
        "entidad",
        ["columna1", "columna2"],
        [["dato1", "dato2"]]
      );
      /* Validacion */
      entidad.nombre = entidad.nombre.split(" ").join("");
      entidad.columnas.forEach((c, c_idx) => {
        entidad.columnas[c_idx] = c.split(" ").join("");
      });
      /* */
      this.servicioIngresarEntidad(entidad).then(res => {
        entidad.entidad_id = res;
        this.entidades.push(entidad);
        this.seleccionarEntidad(this.entidades.length - 1);
      });
    },
    servicioIngresarEntidad(entidad) {
      return new Promise((res, rej) => {
        this.$store.state.servicio.entidad.ingresarEntidad(
          this.tema_id,
          entidad.nombre,
          entidad.columnas,
          entidad.datos,
          response => {
            console.log(response);
            res(JSON.parse(response).entidad_id);
          },
          error => {
            console.log(error);
            rej();
          }
        );
      });
    },
    actualizarEntidad() {
      /* Validacion */
      this.entidad_borrador.nombre = this.entidad_borrador.nombre
        .split(" ")
        .join("");
      this.entidad_borrador.columnas.forEach((c, c_idx) => {
        this.entidad_borrador.columnas[c_idx] = c.split(" ").join("");
      });
      /* */

      let entidad_clone = JSON.parse(JSON.stringify(this.entidad_borrador));
      this.guardar_loading = true;

      this.servicioActualizarEntidad(entidad_clone).then(res => {
        this.guardar_loading = false;
        this.entidades.forEach((e, e_idx) => {
          if (e.entidad_id == entidad_clone.entidad_id) {
            this.entidades[e_idx] = entidad_clone;
            this.seleccionarEntidad(e_idx);
          }
        });
      });
    },
    servicioActualizarEntidad(entidad) {
      return new Promise((res, rej) => {
        this.$store.state.servicio.entidad.actualizarEntidad(
          entidad.entidad_id,
          entidad.nombre,
          entidad.columnas,
          entidad.datos,
          response => {
            console.log(response);
            res();
          },
          error => {
            console.log(error);
            rej();
          }
        );
      });
    },
    eliminarEntidad() {
      this.eliminar_loading = true;
      let entidad_id = this.entidad_borrador.entidad_id;
      console.log(entidad_id);

      this.servicioEliminarEntidad(entidad_id).then(res => {
        this.eliminar_loading = false;
        this.entidades.forEach((e, e_idx) => {
          if (e.entidad_id == entidad_id) {
            this.entidad_borrador = null;
            this.entidades.splice(e_idx, 1);
          }
        });
      });
    },
    servicioEliminarEntidad(entidad_id) {
      return new Promise((res, rej) => {
        this.$store.state.servicio.entidad.eliminarEntidad(
          entidad_id,
          response => {
            console.log(response);
            res(response);
          },
          error => {
            console.log(error);
            rej(error);
          }
        );
      });
    }
  }
};

class Entidad {
  constructor(entidad_id, nombre, columnas, datos) {
    (this.entidad_id = entidad_id),
      (this.nombre = nombre),
      (this.columnas = columnas),
      (this.datos = datos);
  }
}
</script>

<style lang='scss' scoped>
table td,
table th {
  border: none !important;
}
.contenedor-entidad {
  margin-bottom: 20px;
  width: 100%;
}
.contenedor-entidades {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.entidad {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0;
}
.entidad-creado {
  text-align: center;
  padding: 18px 20px;
  font-size: 16px;
  background: linear-gradient(90deg, #918a2c 0%, #bda93b 100%);
}
.entidad-creado:hover {
  box-shadow: 0px 3px 10px 2px rgba(0, 0, 0, 0.2);
}
.entidad-agregar {
  border: 1px dashed #ccc;
  span {
    font-size: 40px;
    font-weight: lighter;
  }
}
.entidad-agregar:hover {
  cursor: pointer;
  border: 1px dashed #ccc;
  background: #f2f2f2;
}
.entidad-loading {
  padding: 18px 0;
  border: 2px solid #efefef;
}
</style>