<template>
  <div>
    <div v-if="cuestionario_borrador" class="pb-1">
      <v-toolbar dense flat>
        <v-btn icon @click="cuestionario_borrador = null">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        <v-text-field hide-details v-model="cuestionario_borrador.nombre"></v-text-field>
        <div class="flex-grow-1"></div>

        <v-btn
          :loading="eliminar_loading"
          v-if="cuestionario_borrador.id"
          icon
          @click="dialog_eliminar_cuestionario = true"
        >
          <v-icon>mdi-delete</v-icon>
        </v-btn>
        <v-btn
          :loading="guardar_loading"
          icon
          @click="actualizarCuestionario(Object.assign({}, cuestionario_borrador))"
        >
          <v-icon>mdi-content-save</v-icon>
        </v-btn>
      </v-toolbar>
      <div class="mx-5 my-3">
        <div>
          <v-col cols="12" md="6">
            <v-select v-model="cuestionario_borrador.nivel" :items="niveles" label="Nivel"></v-select>
          </v-col>
          <v-col cols="12" md="6">
            <v-slider
              :min="0"
              :max="600"
              label="Tiempo (segundos):"
              v-model="cuestionario_borrador.tiempo"
            >
              <template v-slot:append>
                <v-text-field
                  v-model="cuestionario_borrador.tiempo"
                  class="mt-0 pt-0"
                  hide-details
                  single-line
                  type="number"
                  style="width: 60px"
                  steps=1
                ></v-text-field>
              </template>
            </v-slider>
          </v-col>
        </div>
        <v-card>
          <v-tabs v-model="tab" background-color="#287ee4" dark>
            <v-tab v-for="(pregunta, idx_p) in cuestionario_borrador.preguntas" :key="idx_p">
              <strong>{{ idx_p+1 }}</strong>
            </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item
              class="cuestionario-tab mx-auto"
              v-for="(pregunta, idx_p) in cuestionario_borrador.preguntas"
              :key="idx_p"
            >
              <v-card-text class="text-center">
                <v-container>
                  <v-row>
                    <v-col>
                      <div class="mb-5 cuestionario-enunciado">
                        <v-textarea
                          v-model="pregunta.enunciado"
                          auto-grow
                          hide-details
                          rows="1"
                          color="deep-purple"
                          placeholder="Escribe el enunciado aqu"
                          style="margin-bottom: 4px"
                          spellcheck="false"
                        ></v-textarea>
                      </div>
                    </v-col>
                  </v-row>
                  <v-row align="center" justify="center">
                    <v-col
                      cols="12"
                      md="6"
                      v-for="(alternativa, idx_a) in pregunta.alternativas"
                      :key="idx_a"
                    >
                      <div
                        class="d-flex flex-row align-center"
                        style="max-width: 500px; margin: auto"
                      >
                        <div class="elevation-2 px-10 py-5 cuestionario-alternativa">
                          <v-textarea
                            v-model="pregunta.alternativas[idx_a]"
                            auto-grow
                            hide-details
                            rows="1"
                            color="deep-purple"
                            placeholder="Escribe la alternativa aqu"
                            spellcheck="false"
                          ></v-textarea>
                        </div>
                        <div class="ml-2">
                          <v-btn v-if="idx_a == pregunta.correcta" small icon>
                            <v-icon>mdi-checkbox-marked-circle</v-icon>
                          </v-btn>
                          <v-btn
                            v-else
                            small
                            icon
                            @click="asignarAlternativaCorrecta(pregunta, idx_a)"
                          >
                            <v-icon>mdi-checkbox-blank-circle-outline</v-icon>
                          </v-btn>
                        </div>
                      </div>
                    </v-col>
                  </v-row>
                </v-container>
                <v-divider class="my-3"></v-divider>
                <div>
                  <v-btn class="mx-3" color="primary" dark text @click="agregarPregunta(idx_p)">
                    <v-icon left>mdi-plus</v-icon>Agregar
                  </v-btn>
                  <v-btn
                    class="mx-3"
                    v-if="cuestionario_borrador.preguntas.length > 1"
                    text
                    @click="dialog_eliminar_pregunta = true; idx_eliminar_pregunta = idx_p"
                    :color="color_delete"
                    dark
                  >
                    <v-icon left>mdi-minus</v-icon>Eliminar
                  </v-btn>
                </div>
              </v-card-text>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </div>
    </div>
    <div v-else class="mx-3">
      <v-container fluid>
        <v-row>
          <v-col
            cols="12"
            sm="6"
            md="3"
            v-for="(cuestionario, c_idx) in cuestionarios"
            :key="c_idx"
          >
            <v-card
              class="cartel cartel-yellow"
              v-ripple
              dark
              flat
              @click="tab = 0; seleccionarCuestionario(c_idx)"
            >
              <span class="mb-2">{{cuestionario.nombre}}</span>
              <span class="cartel-floating">{{cuestionario['nivel']}}</span>
            </v-card>
          </v-col>
          <v-col v-if="cuestionarios_loading" cols="12" sm="6" md="3">
            <div class="cartel-loading text-center">
              <v-progress-circular :size="30" color="#cfcb5f" indeterminate></v-progress-circular>
            </div>
          </v-col>
          <v-col cols="12" sm="6" md="3">
            <div class="cartel-agregar text-center" @click="agregarCuestionario()">
              <span>+</span>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <v-dialog v-model="dialog_eliminar_cuestionario" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">Desea eliminar el cuestionario seleccionado?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_eliminar_cuestionario = false; eliminarCuestionario()"
              color="red"
            >Eliminar</v-btn>
            <v-btn text @click="dialog_eliminar_cuestionario = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog_eliminar_pregunta" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">Desea eliminar la pregunta seleccionada?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_eliminar_pregunta = false; eliminarPregunta(idx_eliminar_pregunta)"
              color="red"
            >Eliminar</v-btn>
            <v-btn text @click="dialog_eliminar_pregunta = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import firebase, { storage } from "firebase";

export default {
  data() {
    return {
      tab: 0,
      color_delete: "#f96a6a",
      cuestionarios: [],
      cuestionario_borrador: null,
      niveles: ["Bsico", "Intermedio", "Avanzado"],
      //Loadings
      cuestionarios_loading: true,
      eliminar_loading: false,
      guardar_loading: false,
      //Dialogs
      dialog_eliminar_cuestionario: false,
      dialog_eliminar_pregunta: false,
      idx_eliminar_pregunta: 0
    };
  },
  mounted() {
    this.tema_id = this.$route.params["tema_id"];
    this.obtenerCuestionarioPorTema();
  },
  methods: {
    obtenerCuestionarioPorTema() {
      this.$store.state.servicio.cuestionario.obtenerCuestionarioPorTema(
        this.tema_id,
        response => {
          console.log(response);

          this.cuestionarios_loading = false;
          this.cuestionarios = response.map(
            c =>
              new Cuestionario(
                c.cuestionario_id,
                c.nombre,
                c.preguntas,
                c.nivel,
                c.tiempo
              )
          );
        },
        error => {
          this.cuestionarios_loading = false;
          console.log(error);
        }
      );
    },
    seleccionarCuestionario(c_idx) {
      this.cuestionario_borrador = Object.assign({}, this.cuestionarios[c_idx]);
    },
    agregarCuestionario() {
      let cuestionario = new Cuestionario(
        "",
        "Cuestionario",
        [
          new Pregunta(
            "Pregunta",
            [
              "Alternativa 1",
              "Alternativa 2",
              "Alternativa 3",
              "Alternativa 4"
            ],
            0
          )
        ],
        this.niveles[0],
        600
      );

      this.servicioIngresarCuestionario(cuestionario).then(res => {
        cuestionario.id = res;
        this.cuestionarios.push(cuestionario);
        this.seleccionarCuestionario(this.cuestionarios.length - 1);
      });
    },
    servicioIngresarCuestionario(cuestionario) {
      return new Promise((res, rej) => {
        this.$store.state.servicio.cuestionario.ingresarCuestionario(
          this.tema_id,
          cuestionario.nombre,
          cuestionario.preguntas,
          cuestionario.nivel,
          cuestionario.tiempo,
          response => {
            res(response.cuestionario_id);
          },
          error => {
            console.log(error);
            rej();
          }
        );
      });
    },
    actualizarCuestionario(cuestionario_clone) {
      this.guardar_loading = true;
      this.servicioActualizarCuestionario(cuestionario_clone).then(res => {
        this.guardar_loading = false;
        this.cuestionarios.forEach((c, c_idx) => {
          if (c.id == cuestionario_clone.id) {
            this.cuestionarios[c_idx] = cuestionario_clone;
            this.seleccionarCuestionario(c_idx);
          }
        });
      });
    },
    servicioActualizarCuestionario(cuestionario) {
      console.log(cuestionario);
      return new Promise((res, rej) => {
        this.$store.state.servicio.cuestionario.actualizarCuestionario(
          cuestionario.id,
          cuestionario.nombre,
          cuestionario.preguntas,
          cuestionario.nivel,
          cuestionario.tiempo,
          response => {
            console.log(response);
            res();
          },
          error => {
            console.log(error);
            rej();
          }
        );
      });
    },
    eliminarCuestionario() {
      this.eliminar_loading = true;
      let cuestionario_clone = Object.assign({}, this.cuestionario_borrador);

      this.$store.state.servicio.cuestionario.eliminarCuestionario(
        cuestionario_clone.id,
        response => {
          console.log(response);
          this.eliminar_loading = false;
          this.cuestionarios.forEach((c, c_idx) => {
            if (c.id == cuestionario_clone.id) {
              this.cuestionario_borrador = null;
              this.cuestionarios.splice(c_idx, 1);
            }
          });
        },
        error => {
          console.log(error);
        }
      );
    },
    eliminarPregunta(idx) {
      this.cuestionario_borrador.preguntas.splice(idx, 1);
      this.actualizarCuestionario(
        Object.assign({}, this.cuestionario_borrador)
      );
    },
    agregarPregunta(idx) {
      this.cuestionario_borrador.preguntas.splice(
        idx + 1,
        0,
        new Pregunta(
          "Pregunta",
          ["Alternativa 1", "Alternativa 2", "Alternativa 3", "Alternativa 4"],
          0
        )
      );
      this.$nextTick(() => {
        // animacin mas fluida
        this.tab++;
      });
      this.actualizarCuestionario(
        Object.assign({}, this.cuestionario_borrador)
      );
    },
    asignarAlternativaCorrecta(pregunta, idx) {
      pregunta.correcta = idx;
      this.actualizarCuestionario(
        Object.assign({}, this.cuestionario_borrador)
      );
    }
  }
};
class Cuestionario {
  constructor(id, nombre, preguntas, nivel, tiempo) {
    this.id = id;
    this.nombre = nombre;
    this.preguntas = preguntas;
    this.nivel = nivel;
    this.tiempo = tiempo;
  }
}
class Pregunta {
  constructor(enunciado, alternativas, correcta) {
    this.enunciado = enunciado;
    this.alternativas = alternativas;
    this.correcta = correcta;
  }
}
</script>

<style lang="scss">
.cuestionario-tab {
  max-width: 1200px;
  background: #fff;
}
.v-textarea {
  margin: 0;
  padding: 0;
}
</style>