<template>
  <div v-if="material_borrador" class="pb-1">
    <v-toolbar dense flat>
      <v-btn icon @click="material_borrador = null">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-text-field hide-details v-model="material_borrador.nombre"></v-text-field>
      <div class="flex-grow-1"></div>

      <v-btn
        :loading="eliminar_loading"
        v-if="material_borrador.id"
        icon
        @click="dialog_eliminar_material = true"
      >
        <v-icon>mdi-delete</v-icon>
      </v-btn>
      <v-btn
        :loading="guardar_loading"
        icon
        @click="actualizarMaterial(JSON.parse(JSON.stringify(material_borrador)))"
      >
        <v-icon>mdi-content-save</v-icon>
      </v-btn>
    </v-toolbar>
    <div class="mx-5 mt-1 mb-3">
      <v-card class="material-item">
        <v-card-text>
          <div>Texto</div>
          <v-textarea
            v-model="material_borrador.texto"
            auto-grow
            hide-details
            rows="1"
            color="deep-purple"
            placeholder="Texto"
            style="margin-bottom: 4px"
            spellcheck="false"
          ></v-textarea>
        </v-card-text>
      </v-card>

      <v-card class="material-item">
        <v-card-text>
          <div>Importancia</div>
          <v-textarea
            v-model="material_borrador.importancia"
            auto-grow
            hide-details
            rows="1"
            color="deep-purple"
            placeholder="Importancia"
            style="margin-bottom: 4px"
            spellcheck="false"
          ></v-textarea>
        </v-card-text>
      </v-card>

      <v-card class="material-item">
        <v-card-text>
          <div>Ejemplos</div>
          <div v-for="(ejemplo, e_idx) in material_borrador.ejemplos" :key="e_idx">
            <v-textarea
              v-model="material_borrador.ejemplos[e_idx]"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Ejemplo"
              style="margin-bottom: 4px"
              spellcheck="false"
            >
              <template v-slot:append-outer>
                <v-btn
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="red"
                  @click="eliminarEjemplo(e_idx)"
                >
                  <v-icon dark>mdi-minus</v-icon>
                </v-btn>
              </template>
            </v-textarea>
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn @click="agregarEjemplo()" text color="success" class="mb-2">
            Agregar
            <v-icon class="ml-2">mdi-plus</v-icon>
          </v-btn>
          <div class="flex-grow-1"></div>
        </v-card-actions>
      </v-card>

      <v-card class="material-item">
        <v-card-text>
          <div>Prueba</div>
          <div class="material-quiz" v-for="(quiz, q_idx) in material_borrador.quiz" :key="q_idx">
            <v-textarea
              v-model=" quiz.enunciado"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Enunciado"
              style="margin-bottom: 4px"
              spellcheck="false"
            >
              <template v-slot:append-outer>
                <v-btn
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="red"
                  @click="eliminarQuiz(q_idx)"
                >
                  <v-icon dark>mdi-delete</v-icon>
                </v-btn>
              </template>
            </v-textarea>
            <v-radio-group v-model="quiz.correcta" :mandatory="false">
              <div class="d-flex" v-for="(alternativa, a_idx) in quiz.alternativas" :key="a_idx">
                <v-radio :value="a_idx"></v-radio>
                <v-textarea
                  v-model="quiz.alternativas[a_idx]"
                  auto-grow
                  hide-details
                  rows="1"
                  color="deep-purple"
                  :placeholder="'Alternativa ' + (a_idx + 1)"
                  style="margin-bottom: 4px"
                  spellcheck="false"
                ></v-textarea>
              </div>
            </v-radio-group>
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn @click="agregarQuiz()" text color="success" class="mb-2">
            Agregar
            <v-icon class="ml-2">mdi-plus</v-icon>
          </v-btn>
          <div class="flex-grow-1"></div>
        </v-card-actions>
      </v-card>

      <v-card class="material-item">
        <v-progress-linear v-if="progress_img.val != 0" :value="progress_img.val"></v-progress-linear>
        <v-card-text>
          <div>Imagen</div>
          <div class="material-imagen">
            <img :src="material_borrador.imagen" alt />
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn onclick="upload_img.click()" text icon color="success">
            <v-icon>mdi-upload</v-icon>
          </v-btn>
          <v-btn @click="eliminarFile(material_borrador.imagen, 'imagen')" text icon color="error">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-file-input
            id="upload_img"
            @change="onFileSelected(file_img, progress_img, 'imagen')"
            v-model="file_img"
            style="display:none"
          ></v-file-input>
        </v-card-actions>
      </v-card>

      <v-card class="material-item">
        <v-progress-linear v-if="progress_vid.val != 0" :value="progress_vid.val"></v-progress-linear>
        <v-card-text>
          <div>Video</div>
          <div>
            <v-text-field v-model="aux_video">
              <template v-slot:append-outer>
                <v-btn @click="actualizarVideo()" text icon color="primary">
                  <v-icon>mdi-sync</v-icon>
                </v-btn>
              </template>
            </v-text-field>
          </div>
          <div v-if="material_borrador.video" class="material-video">
            <div class="aspect-ratio-video">
              <iframe
                :src="material_borrador.video"
                :key="material_borrador.video"
                frameborder="0"
                allowfullscreen
                class="aspect-ratio-item"
              />
            </div>
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn onclick="upload_vid.click()" text icon color="success">
            <v-icon>mdi-upload</v-icon>
          </v-btn>
          <v-btn @click="eliminarFile(material_borrador.video, 'video')" text icon color="error">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-file-input
            id="upload_vid"
            @change="onFileSelected(file_vid, progress_vid, 'video')"
            v-model="file_vid"
            style="display:none"
          ></v-file-input>
        </v-card-actions>
      </v-card>

      <v-card class="material-item">
        <v-progress-linear v-if="progress_doc.val != 0" :value="progress_doc.val"></v-progress-linear>
        <v-card-text>
          <div>Documento</div>
          <div>
            <v-text-field v-model="aux_documento">
              <template v-slot:append-outer>
                <v-btn @click="actualizarDocumento()" text icon color="primary">
                  <v-icon>mdi-sync</v-icon>
                </v-btn>
              </template>
            </v-text-field>
          </div>
          <div v-if="material_borrador.documento" class="material-documento">
            <div class="aspect-ratio-documento">
              <embed
                :src="material_borrador.documento"
                :key="material_borrador.documento"
                frameborder="0"
                class="aspect-ratio-item"
              />
            </div>
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn onclick="upload_doc.click()" text icon color="success">
            <v-icon>mdi-upload</v-icon>
          </v-btn>
          <v-btn
            @click="eliminarFile(material_borrador.documento, 'documento')"
            text
            icon
            color="error"
          >
            <v-icon>mdi-delete</v-icon>
          </v-btn>
          <v-file-input
            id="upload_doc"
            @change="onFileSelected(file_doc, progress_doc, 'documento')"
            v-model="file_doc"
            style="display:none"
          ></v-file-input>
        </v-card-actions>
      </v-card>

      <v-card class="material-item">
        <v-card-text>
          <div>Preguntas Frecuentes</div>
          <div class="material-faq" v-for="(f, f_idx) in material_borrador.faq" :key="f_idx">
            <v-textarea
              v-model=" f.pregunta"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Pregunta"
              style="margin-bottom: 4px"
              spellcheck="false"
            >
              <template v-slot:append-outer>
                <v-btn
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="red"
                  @click="eliminarFAQ(f_idx)"
                >
                  <v-icon dark>mdi-minus</v-icon>
                </v-btn>
              </template>
            </v-textarea>
            <v-textarea
              v-model=" f.respuesta"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Respuesta"
              style="margin-bottom: 4px"
              spellcheck="false"
            ></v-textarea>
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn @click="agregarFAQ()" text color="success" class="mb-2">
            Agregar
            <v-icon class="ml-2">mdi-plus</v-icon>
          </v-btn>
          <div class="flex-grow-1"></div>
        </v-card-actions>
      </v-card>

      <v-card class="material-item">
        <v-card-text>
          <div>Enlaces</div>
          <div
            class="material-explicacion"
            v-for="(e, e_idx) in material_borrador.explicacion"
            :key="e_idx"
          >
            <v-textarea
              v-model=" e.nombre"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Nombre"
              style="margin-bottom: 4px"
              spellcheck="false"
            >
              <template v-slot:append-outer>
                <v-btn
                  slot="append"
                  class="mx-2"
                  text
                  icon
                  small
                  color="red"
                  @click="eliminarExplicacion(e_idx)"
                >
                  <v-icon dark>mdi-minus</v-icon>
                </v-btn>
              </template>
            </v-textarea>
            <v-textarea
              v-model=" e.enlace"
              auto-grow
              hide-details
              rows="1"
              color="deep-purple"
              placeholder="Enlace"
              style="margin-bottom: 4px"
              spellcheck="false"
            ></v-textarea>
          </div>
        </v-card-text>

        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn @click="agregarExplicacion()" text color="success" class="mb-2">
            Agregar
            <v-icon class="ml-2">mdi-plus</v-icon>
          </v-btn>
          <div class="flex-grow-1"></div>
        </v-card-actions>
      </v-card>

    </div>
    <v-dialog v-model="dialog_eliminar_material" max-width="290">
      <v-card>
        <v-card class="text-center">
          <v-card-text style="font-size: 16px">Desea eliminar el material seleccionado?</v-card-text>
          <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn
              text
              @click="dialog_eliminar_material = false; eliminarMaterial()"
              color="red"
            >Eliminar</v-btn>
            <v-btn text @click="dialog_eliminar_material = false">Cancelar</v-btn>
            <div class="flex-grow-1"></div>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
  </div>
  <div v-else class="mx-3">
    <v-container fluid>
      <v-row>
        <v-col cols="12" sm="6" md="3" v-for="(material, m_idx) in materiales" :key="m_idx">
          <v-card class="cartel cartel-blue" v-ripple dark flat @click="seleccionarMaterial(m_idx)">
            <span>{{material.nombre}}</span>
          </v-card>
        </v-col>
        <v-col v-if="materiales_loading" cols="12" sm="6" md="3">
          <div class="cartel-loading text-center">
            <v-progress-circular :size="30" color="primary" indeterminate></v-progress-circular>
          </div>
        </v-col>
        <v-col cols="12" sm="6" md="3">
          <div class="cartel-agregar text-center" @click="agregarMaterial()">
            <span>+</span>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  data() {
    return {
      materiales: [],
      material_borrador: null,
      file_img: null,
      file_vid: null,
      file_doc: null,
      progress_img: { val: 0 },
      progress_vid: { val: 0 },
      progress_doc: { val: 0 },
      aux_video: "",
      aux_documento: "",
      //Loadings
      materiales_loading: true,
      eliminar_loading: false,
      guardar_loading: false,
      //Dialog
      dialog_eliminar_material: false
    };
  },
  mounted() {
    this.tema_id = this.$route.params["tema_id"];
    this.sesion = this.$store.state.servicio.sesion.obtenerSesion();
    this.obtenerMaterialPorTema();
  },
  methods: {
    obtenerMaterialPorTema() {
      this.$store.state.servicio.material.obtenerMaterialPorTema(
        this.tema_id,
        response => {
          this.materiales_loading = false;
          JSON.parse(response).forEach(m => {
            this.materiales.push(
              new Material(
                m._id.$oid,
                m.nombre,
                m.texto,
                m.ejemplos || [],
                m.documento,
                m.video,
                m.imagen,
                m.quiz || [],
                m.importancia || "",
                m.explicacion,
                m.faq
              )
            );
          });
        },
        error => {
          this.materiales_loading = false;
          console.log(error);
        }
      );
    },
    agregarFAQ() {
      this.material_borrador.faq.push(new FAQ("", ""));
    },
    eliminarFAQ(f_idx) {
      this.material_borrador.faq.splice(f_idx, 1);
    },
    agregarExplicacion() {
      this.material_borrador.explicacion.push(new Explicacion("", ""));
    },
    eliminarExplicacion(e_idx) {
      this.material_borrador.explicacion.splice(e_idx, 1);
    },
    agregarQuiz() {
      this.material_borrador.quiz.push(new Quiz("", ["", "", "", ""], 0));
    },
    eliminarQuiz(q_idx) {
      this.material_borrador.quiz.splice(q_idx, 1);
    },
    agregarEjemplo() {
      this.material_borrador.ejemplos.push("");
    },
    eliminarEjemplo(e_idx) {
      this.material_borrador.ejemplos.splice(e_idx, 1);
    },
    actualizarVideo() {
      try {
        var desertRef = firebase
          .storage()
          .refFromURL(this.material_borrador.video);
        desertRef
          .delete()
          .then(() => {})
          .catch(error => {
            console.log(error);
          });
      } catch (error) {}
      this.material_borrador.video = this.aux_video;
      this.actualizarMaterial(
        JSON.parse(JSON.stringify(this.material_borrador))
      );
    },
    actualizarDocumento() {
      try {
        var desertRef = firebase
          .storage()
          .refFromURL(this.material_borrador.documento);
        desertRef
          .delete()
          .then(() => {})
          .catch(error => {
            console.log(error);
          });
      } catch (error) {}
      this.material_borrador.documento = this.aux_documento;
      this.actualizarMaterial(
        JSON.parse(JSON.stringify(this.material_borrador))
      );
    },
    onFileSelected(file, progress, value) {
      let material_clone = JSON.parse(JSON.stringify(this.material_borrador));
      // Eliminando el archivo anterior
      if (material_clone[value] && file != null) {
        try {
          var desertRef = firebase.storage().refFromURL(material_clone[value]);
          desertRef
            .delete()
            .then(() => {})
            .catch(error => {
              console.log(error);
            });
        } catch (error) {}
      }
      // Subiendo el nuevo archivo
      let promise = this.subirFile(file, progress);
      // Actualizando el material en la BD
      if (promise)
        promise.then(res => {
          file = null;
          progress.val = 0;
          material_clone[value] = res;
          this.actualizarMaterial(material_clone);
        });
    },
    seleccionarMaterial(m_idx) {
      (this.file_img = null), (this.file_vid = null);
      this.material_borrador = JSON.parse(
        JSON.stringify(this.materiales[m_idx])
      );
      this.aux_video = this.material_borrador.video;
      this.aux_documento = this.material_borrador.documento;
    },
    agregarMaterial() {
      let material = new Material("", "Titulo", "", [], "", "", "", [], "");
      this.servicioIngresarMaterial(material).then(res => {
        material.id = res;
        this.materiales.push(material);
        this.seleccionarMaterial(this.materiales.length - 1);
      });
    },
    servicioIngresarMaterial(material) {
      return new Promise((res, rej) => {
        this.$store.state.servicio.material.ingresarMaterial(
          this.tema_id,
          material.nombre,
          material.texto,
          material.documento,
          material.video,
          material.imagen,
          material.quiz,
          material.ejemplos,
          material.importancia,
          response => {
            res(response.material_id);
          },
          error => {
            console.log(error);
            rej();
          }
        );
      });
    },
    actualizarMaterial(material_clone) {
      this.guardar_loading = true;
      this.servicioActualizarMaterial(material_clone).then(res => {
        this.guardar_loading = false;
        this.materiales.forEach((m, m_idx) => {
          if (m.id == material_clone.id) {
            this.materiales[m_idx] = material_clone;
            this.seleccionarMaterial(m_idx);
          }
        });
      });
    },
    servicioActualizarMaterial(material) {
      return new Promise((res, rej) => {
        this.$store.state.servicio.material.actualizarMaterial(
          material.id,
          this.tema_id,
          material.nombre,
          material.texto,
          material.documento,
          material.video,
          material.imagen,
          material.quiz,
          material.ejemplos,
          material.importancia,
          material.explicacion,
          material.faq,
          response => {
            console.log(response);
            res();
          },
          error => {
            console.log(error);
            rej();
          }
        );
      });
    },
    eliminarMaterial() {
      this.eliminar_loading = true;
      let material_clone = JSON.parse(JSON.stringify(this.material_borrador));
      let promises = [];

      promises.push(
        new Promise((res, rej) => {
          if (material_clone.imagen) {
            var desertRef = firebase
              .storage()
              .refFromURL(material_clone.imagen);
            desertRef
              .delete()
              .then(() => {
                res();
              })
              .catch(error => {
                rej();
              });
          } else {
            res();
          }
        })
      );

      promises.push(
        new Promise((res, rej) => {
          if (material_clone.video) {
            var desertRef = firebase.storage().refFromURL(material_clone.video);
            desertRef
              .delete()
              .then(() => {
                res();
              })
              .catch(error => {
                rej();
              });
          } else {
            res();
          }
        })
      );

      promises.push(
        new Promise((res, rej) => {
          this.$store.state.servicio.material.eliminarMaterial(
            material_clone.id,
            response => {
              console.log(response);
              res();
            },
            error => {
              console.log(error);
              rej();
            }
          );
        })
      );

      Promise.all(promises).then(res => {
        this.eliminar_loading = false;
        this.materiales.forEach((m, m_idx) => {
          if (m.id == material_clone.id) {
            this.material_borrador = null;
            this.materiales.splice(m_idx, 1);
          }
        });
      });
    },
    subirFile(file, progress) {
      if (file != null) {
        let storageRef = firebase
          .storage()
          // Profesor/Tema/
          .ref(
            `/Material/${this.sesion["id"]}/${
              this.tema_id
            }/${this.createUUID()}`
          );
        let task = storageRef.put(file);

        return new Promise((res, rej) => {
          task.on(
            "state_changed",
            snapshot => {
              progress.val =
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            },
            error => {
              console.log(error.message);
              rej();
            },
            () => {
              progress.val = 100;
              task.snapshot.ref.getDownloadURL().then(url => {
                res(url);
              });
            }
          );
        });
      }
    },
    eliminarFile(url, value) {
      let material_clone = JSON.parse(JSON.stringify(this.material_borrador));
      var desertRef = firebase.storage().refFromURL(url);
      desertRef
        .delete()
        .then(() => {
          material_clone[value] = "";
          this.actualizarMaterial(material_clone);
        })
        .catch(error => {
          console.log(error);
        });
    },
    createUUID() {
      var dt = new Date().getTime();
      var uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function(c) {
          var r = (dt + Math.random() * 16) % 16 | 0;
          dt = Math.floor(dt / 16);
          return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
      return uuid;
    }
  }
};
class Material {
  constructor(
    id,
    nombre,
    texto,
    ejemplos,
    documento,
    video,
    imagen,
    quiz,
    importancia,
    explicacion,
    faq
  ) {
    this.id = id;
    this.nombre = nombre;
    this.texto = texto;
    this.ejemplos = ejemplos;
    this.documento = documento;
    this.video = video;
    this.imagen = imagen;
    this.quiz = quiz;
    this.importancia = importancia;
    this.explicacion = explicacion;
    this.faq = faq;
  }
}
class Quiz {
  constructor(enunciado, alternativas, correcta) {
    this.enunciado = enunciado;
    this.alternativas = alternativas;
    this.correcta = correcta;
  }
}
class FAQ {
  constructor(pregunta, respuesta) {
    this.pregunta = pregunta;
    this.respuesta = respuesta;
  }
}
class Explicacion {
  constructor(nombre, enlace) {
    this.nombre = nombre;
    this.enlace = enlace;
  }
}
</script>

